import React, { useState,useEffect,useCallback } from "react";
import { connect } from "dva";
import {lanlan,T} from './type';

interface TT{
  name:number,
  age:number
}
interface FF{
  name:Boolean,
  age:string
}
interface TTT{
  testTwo:T,
  getIndexListData:(arg0: TT)=>void

}
const Example = (props:TTT) => {
  console.log(props.testTwo);
  const [count, setCount] = useState(0);
  const [counts, setCounts] = useState(0);

 
  useEffect( ()=>{
     console.log('zhzuhzuzhzu');
     props.getIndexListData( {name:22,age:23}   )
  },[] )

  const nameA=useCallback(()=>{
    console.log('是否更新')
     return <div>lanlan</div>
  },[count])

  return (
    <div>
      <p>You clicked {count} times</p>
      <button onClick={() => setCount(count + 1)}>Click me</button>
      

       <p>这是检测的是否更新</p>


      <p>You clicked {counts} times</p>
      <button onClick={() => setCounts(counts + 1)}>Click me</button>
      {nameA()}
    </div>
  );
};
export default connect(
  (state: lanlan) => {
    return {
      testTwo:state.testTwo,
      name:state.name
    };
  },
  (dispatch: any) => {
    return {
      getIndexListData: (payload:any) => {
       
        console.log(payload)

        dispatch({
          type:'testTwo/getlist',
          payload:{name:'lanlan',lover:'isnotme'}
        })

        dispatch({
            type: 'testTwo/save',
            payload: payload,
        });
        
       },

    };
  }
)(Example);
